.card-fitures-container {
    display: flex;
    position: relative;
    width: 100%;
}

.card-border {
    position: absolute;
    background-color: var(--cyan);
}

/* Small devices (landscape phones, 376px, 576px and up) */

@media (max-width: 576px) {
    .card-fitures-container {
        flex-direction: column;
        align-items: center;
    }

    .card-border {
        top: 0;
        left: 50%;
        width: 8px;
        height: 100%;
    }

    .card-fitures-container > .card-container:nth-child(2){
        margin-top: 0px;
    }
    
    .card-fitures-container > .card-container:nth-child(3){
        margin-top: 0px;
    }
    
    .card-fitures-container > .card-container:nth-child(4){
        margin-top: 0px;
    }

    .card-fitures-container > .card-container:not(:nth-child(4)) {
        margin-bottom: 100px;
    }
    
}

@media (min-width: 577px) {
    .card-fitures-container {
        flex-direction: column;
        align-items: center;
    }

    .card-border {
        top: 0;
        left: 50%;
        width: 8px;
        height: 100%;
    }

    .card-fitures-container > .card-container:nth-child(2){
        margin-top: 0px;
    }
    
    .card-fitures-container > .card-container:nth-child(3){
        margin-top: 0px;
    }
    
    .card-fitures-container > .card-container:nth-child(4){
        margin-top: 0px;
    }

    .card-fitures-container > .card-container:not(:nth-child(4)) {
        margin-bottom: 100px;
    }
    
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) { 

    .card-fitures-container {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        position: relative;
        width: 100%;
    }
    
    .card-border {
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;
        height: 8px;
    }
    
    .card-fitures-container > .card-container:nth-child(2){
        margin-top: 0px;
    }
    
    .card-fitures-container > .card-container:nth-child(3){
        margin-top: 80px;
    }
    
    .card-fitures-container > .card-container:nth-child(4){
        margin-top: 120px;
    }

 }

